<script>
export default {
    data(){
        return {
            clock:null,
        }
    },
    mounted(){
        var frameCount=8
        var currentFrame = 1;
        var previousFrame = frameCount;
        const self=this
        clearInterval(this.clock)
        this.clock=setInterval(function () {
            self.$refs[`frame-0${currentFrame}`].style.visibility = 'visible'
            self.$refs[`frame-0${previousFrame}`].style.visibility = 'hidden'
            if (currentFrame < frameCount) {
                currentFrame += 1;
                previousFrame = currentFrame - 1;
            } else {
                currentFrame = 1;
                previousFrame = frameCount;
            }
        }, 75)
    },
    unmounted(){
        clearInterval(this.clock)
    }
};
</script>